@import "mixins";
@import "colors";

.appEventsReport {
    &-period {
        margin-bottom: 16px;

        @media all and (min-width: 1200px) {
            display: flex;
            justify-content: space-between;
        }

        &Title {
            color: $txt_grey;
            font-size: 13px;
            font-weight: 500;
        }
    }
    &-filters {
        border-radius: 8px;
        border: 1px solid $border_color;
        padding: 16px;
        margin-bottom: 16px;
        background: $bg-odd;

        &Title {
            color: $txt_grey;
            font-size: 13px;
            font-weight: 500;
        }

        &Items {
            @include flex-jb-ac;
            @include child-mr8;

            @media all and (min-width: 1024px) {
                margin-left: 64px;
            }
        }

    }
    &-head {
        @include flex-jl-as;
        @include child-mr8;
        padding: 8px 0;
        font-weight: bold;

        &Row {
            flex-shrink: 0;
            flex-grow: 0;

            &.region {
                width: 100px;
            }

            &.bhukti {
                width: 200px;
            }

            &.unit {
                width: 100px;
            }

            &.range {
                width: 50px;
            }

            &.event {

                @include textOverflowRow;
                width: 50px;
            }
        }
    }
    &-region {
        @include flex-jl-as;
        @include child-mr8;


        &Info {
            width: 100px;
            flex-shrink: 0;
        }

        &Content {
            &Bhukti {
                @include flex-jl-as;
                @include child-mr8;

                &:nth-child(odd) {
                    background: $bg-odd;
                }

                &Info {
                    width: 200px;

                    .bhuktiName {
                        color: $pastel1-icon;
                    }
                    .bhuktiBP {
                        color: $pastel1-button-bg;
                    }
                }

                &Content {
                    &Unit {
                        @include flex-jl-as;
                        @include child-mr8;
                        &:nth-child(even) {
                            border-bottom: 1px solid $border;
                        }
                        &:nth-child(odd) {
                            border-bottom: 1px solid $border-color;
                        }

                        &Info {
                            @include flex-jl-as;
                            @include child-mr8;
                            &Name {
                                width: 100px;
                            }
                            &MargiisActive {
                                width: 30px;
                            }
                        }
                        &Column {

                            width: 50px;


                            &Bottom {
                                .diff {
                                    font-size: 10px;
                                    letter-spacing: -1px;
                                }
                                .red {
                                    color: $pastel-red;
                                }
                                .green {
                                    color: $pastel-green;
                                }
                            }
                        }

                        &Range {
                            width: 50px;
                        }
                    }
                }
            }
        }
    }

}

